.cartItems{
    margin: 100px 170px;
}
.cartItems hr{
   height: 3px;
   border: 0;
   background: #e2e2e2;
}
.cartitems-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format {
    font-size: 18px;
    font-weight: 500;
}
.carticon-product-icon{
    height: 62px;   
}
.cartItems-remove-icon{
   width: 15px;
   margin: 0px 40px;
   cursor: pointer;
}
.cartitems-quantity{
    display: flex;
    gap: 10px;
    width: 64px;
    height: 50px;
    outline: none;
}
.cartitems-quantity button{
    flex: 1;
    height: 50%;
    border: 1px solid #e2e2e2;
    outline: none;
    background: transparent;
    cursor: pointer;
}

.cartitems-down{
    display: flex;
    margin: 100px 0px;
}
.cartitems-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-top: 200px;
    gap: 40px;
}
.cartitems-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitems-total button{
    width: 282px;
    height: 58px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
}
.cartitems-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitems-promocode p{
    color: #555;
}
.cartitems-promobox{
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #eaeaea;
}
.cartitems-promobox input{
    width: 330px;
    height: 50px;
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    font-weight: 500;
}
.cartitems-promobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    cursor: pointer;
}